@import 'iconsList';

@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?z1ebpz');
  src:  url('../fonts/icomoon.eot?z1ebpz#iefix') format('embedded-opentype'),
  url('../fonts/icomoon.ttf?z1ebpz') format('truetype'),
  url('../fonts/icomoon.woff?z1ebpz') format('woff'),
  url('../fonts/icomoon.svg?z1ebpz#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  font-size: 29px;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}